.login_body {
    display: grid;
    grid-template-columns: auto auto auto;
    margin-top: 20vmin;
    justify-content: center;
}

.login_top_text {
    display: grid;
    grid-column: 1/4;
    justify-content: center;
    font-family: var(--font-family-Comfortaa);
    font-size: 6vmin;
    text-transform: capitalize;
    color: var(--color-text);
    font-weight: 600;
}

.email_input:focus,
.password_input:focus {
    border: 0.6vmin solid #32B126;
    border-image-slice: 1;
}
.email_input {
    display: grid;
    margin-top: 4vmin;
    margin-left: auto;
    margin-right: auto;
    grid-column: 1/4;
    outline: 0;
    padding: 3vmin;
    border: 0.5vmin solid #A4BFA2;
    border-radius: 3vmin;
    height: 9vmin;
    width: 64vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3vmin;
    color: #32b126;
}

.password_input {
    grid-column: 1/4;
    display: grid;
    margin-top: 5vmin;
    margin-left: auto;
    margin-right: auto;
    outline: 0;
    padding: 3vmin;
    border: 0.5vmin solid #A4BFA2;
    border-radius: 3.5vmin;
    height: 9vmin;
    width: 64vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3vmin;
    color: #32b126;
}

small {
    justify-content: end;
    display: grid;
    grid-column: 1/4;
    margin-right: 6.5vmin;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.5vmin;
    letter-spacing: 0.05vmin;
    text-decoration-line: underline;
    text-transform: capitalize;
    color: var(--color-text);
    font-weight: 400;
}

.remember_text {
    margin-left: 2vmin;
    margin-top: 2vmin;
    display: grid;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.9vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
    font-weight: 400;
}

.remember_check {
    margin-top: 2.7vmin;
    margin-left: 8vmin;
    display: grid;
    width: 2.8vmin;
    height: 2.8vmin;
    color:#32B126;
    cursor: pointer;
}

.btn_login {
    display: grid;
    grid-column: 1/4;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2vmin;
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
    padding-top: 1.7vmin;
    outline: 0;
    border-radius: 2.5vmin;
    border: none;
    font-family: var(--font-family-Comfortaa);
    font-size: 3.5vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: #FFFFFF;
    height: 7.8vmin;
    width: 17vmin
}

.btn_login:hover {
    background-color: #32B126;
}

.dont_have_account {
    grid-column: 1/4;
    display: grid;
    margin-top: 5vmin;
    margin-left: auto;
    margin-right: auto;
    font-family: var(--font-family-Comfortaa);
    font-weight: 600;
    font-size: 2.6vmin;
    letter-spacing: 0.01vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: var(--color-text);
}