.signup_body {
    display: grid;
    grid-template-columns: auto auto;
    margin-top: 16vmin;
    justify-content: center;
}

.signup_top_text {
    display: grid;
    grid-column: 1/3;
    justify-content: center;
    font-family: var(--font-family-Comfortaa);
    font-size: 5vmin;
    text-transform: capitalize;
    color: var(--color-text);
    font-weight: 600;
}

.signup_canidate_btn {
    display: grid;
    margin-left: 40vmin;
    border-radius: 3vmin 0vmin 0vmin 3vmin;
}

.signup_company_btn {
    display: grid;
    border-radius: 0vmin 3vmin 3vmin 0vmin;
}

.btn_company_candiate_text {
    border-style: solid;
    border-color: #B8DEF3;
    cursor: pointer;
    text-transform: capitalize;
    font-family: var(--font-family-Urbanist);
    font-size: 3vmin;
    color: var(--color-text);
    padding: 1.5vmin;
    width: 26vmin;
}

.signup_input {
    display: grid;
    margin-top: 2vmin;
    margin-bottom: 2vmin;
    height: 9vmin;
    width: 64vmin;
    border: 0.55vmin solid #CECECE;
    border-radius: 2.5vmin;

    outline: 0;
    padding: 3vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3vmin;
    color: #32b126;
}
.signup_input:focus {
    border: 0.6vmin solid #32B126;
    border-image-slice: 1;
}

.signup_btn_login {
    display: grid;
    grid-column: 1/3;
    justify-content: center;
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
    padding: 2.4vmin 4vmin;
    outline: 0;
    border-radius: 2.5vmin;
    border: none;
    font-family: var(--font-family-Comfortaa);
    font-size: 3.5vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: #FFFFFF;
    height: 7.8vmin;
    width: 17vmin;
    padding-top: 1.3vmin;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2vmin;
    margin-top: 2vmin;
}

.signup_have_account {
    display: grid;
    grid-column: 1/3;
    justify-content: center;
    font-family: var(--font-family-Comfortaa);
    font-weight: 700;
    font-size: 2.9vmin;
    letter-spacing: 0.01vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: var(--color-text);
}

.btn_active {
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.3) 1.99%, rgba(36, 162, 233, 0.3) 85.34%, rgba(36, 162, 233, 0.3) 104.58%);
}

.signup_email_input {
    margin-right: 2vmin;
}

.signup_password_input {
    margin-right: 2vmin;
}

.signup_fname_input {
    margin-right: 2vmin;
}

.signup_lname_input {
    margin-left: 2vmin;
}

.signup_phone_input {
    margin-left: 2vmin;
}

.signup_cpassword_input {
    margin-left: 2vmin;
}