.academic_section_left {
    position: absolute;
    width: 70vmin;
}

.academic_left_grid {
    display: grid;
    margin-top: 10.6vmin;
    margin-left: 10vmin;
}

.academic_left_grid_iteam {
    display: grid;
    font-family: var(--font-family-Urbanist);
    letter-spacing: 0.03em;
    color: var(--color-text);
}

.adademic_left_heading_1 {
    font-weight: 700;
    font-size: 3vmin;
}

.adademic_left_heading_2 {
    font-weight: 500;
    font-size: 2.7vmin;
    margin-top: 6vmin;
}

.adademic_left_heading_3 {
    font-weight: 500;
    font-size: 2.7vmin;
    margin-top: 19vmin;
}

.adademic_left_heading_4 {
    font-weight: 500;
    font-size: 2.4vmin;
    width: 40vmin;
    color: #0A5F8F;
}








.academic_section_right {
    position: absolute;
    left: 70vmin;
}

.academic_right_grid {
    display: grid;
    margin-top: 10vmin;
    grid-row-gap: 2.5vmin;
    grid-template-columns: auto auto auto auto auto auto auto auto;
}

.academic_top_text {
    display: grid;
    grid-column: 1/9;
    font-family: var(--font-family-Urbanist);
    color: var(--color-text);
    font-weight: 500;
}

.adademic_uni_name {
    display: grid;
    grid-column: 1/3;
}

.adademic_degree {
    display: grid;
    grid-column: 3/9;
    margin-left: 2vmin;
    width: 75vmin;
}

.adademic_s_year {
    display: grid;
    grid-column: 1/2;
    margin-right: 2vmin;
    width: 35vmin;
}

.adademic_e_year {
    display: grid;
    grid-column: 2/3;
    width: 35vmin;
    margin-left: 2.5vmin;
}

.adademic_o_cgpa {
    display: grid;
    grid-column: 3/4;
    width: 35vmin;
    margin-left: 2.5vmin;
}

.adademic_t_cgpa {
    display: grid;
    grid-column: 4/9;
    width: 35vmin;
    margin-left: 4.4vmin;
}

.academic_input {
    display: grid;
    padding: 1vmin;
    outline: 0;
    border: 0.5vmin solid #A4BFA2;
    border-radius: 1.5vmin;
    height: 7.5vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 2.5vmin;
    letter-spacing: 0.1vmin;
    color: #32B126;
    background-color: white;
}

.adademic_checkbox {
    display: grid;
    grid-column: 1/1;
    width: 2.7vmin;
    height: 2.7vmin;
}

.adademic_checkbox_label {
    position: absolute;
    top: 38.7vmin;
    left: 4vmin;
    font-family: var(--font-family-Urbanist);
    color: var(--color-text);
    font-weight: 400;
    font-size: 2.5vmin;
}

.adademic_textarea {
    display: grid;
    grid-column: 1/9;
    border-radius: 3vmin;
    border: 3px solid #A4BFA2;
    padding: 2vmin;
    outline: 0;
}

.adademic_text_area_label {
    display: grid;
    grid-column: 7/9;

}

.acdademic_Add_btn {
    display: grid;
    grid-column: 1/9;
    border-radius: 100%;
    width: 5vmin;
    height: 5vmin;
    color: var(--color-text);
    border: 0.4vmin solid var(--color-text);
    background: transparent;
    align-items: center;
}

.adademic_Add_btn_label {
    position: absolute;
    top: 69.3vmin;
    left: 7vmin;
    font-family: var(--font-family-Urbanist);
    color: var(--color-text);
    font-weight: 400;
    font-size: 2.5vmin;
}

.adademic_next_btn {
    display: grid;
    grid-column: 2/9;
    padding-top: 1.5vmin;
    background: linear-gradient(98.45deg, #32B126 -9.85%, #24A2E9 97.35%);
    border: 0;
    height: 6.5vmin;
    width: 16vmin;
    border-radius: 2.2vmin;
    color: #fff;
    font-family: var(--font-family-Comfortaa);
    font-weight: 600;
}