.profile_body {
    width: 100vw;
    height: 100vh;
}
.profile_lable {
    position: absolute;
    text-transform: capitalize;
    position: absolute;
    font-family: var(--font-family-Urbanist);
    font-size: 2.5vmin;
    letter-spacing: 0.01vmin;
    color: var(--color-text);
    background-color: #fff;
}
.profile_input:focus{
    border: 0.6vmin solid;
    mask-image: linear-gradient(89.95deg, rgba(36, 162, 233, 0.8) 1.99%, rgba(50, 177, 38, 0.8) 104.58%);
    border-image-slice: 1;
}
.profile_input {
    padding: 1vmin;
    outline: 0;
    position:absolute;
    border: 0.5vmin solid #A4BFA2;
    border-radius: 1.5vmin;
    height: 7.5vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 2.5vmin;
    letter-spacing: 0.1vmin;
    color: #32B126;
    background-color:white;
}

