.login_dark_glass {
    position: absolute;
    left: 4.9vmin;
    top: 5.7vmin;
    bottom: 5.7vmin;
    right: 4.9vmin;
    background: #0C0B38;
    border-radius: 6vmin;
}

.login_dark_top_text {
    margin-top: 16vmin;
    text-align: center;
    font-family: var(--font-family-Comfortaa);
    font-size: 6.5vmin;
    text-transform: capitalize;
    color: #EBEFFF;
}

.login_dark_email_input:focus,
.login_dark_password_input:focus {
    border-image-source: linear-gradient(89.95deg, rgba(36, 162, 233, 0.8) 1.99%, rgba(50, 177, 38, 0.8) 104.58%);
    border-image-slice: 1;
}

.login_dark_input_label_email {
    position: absolute;
    left: 74vmin;
    top: 30.6vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.2vmin;
    letter-spacing: 0.05vmin;
    color: #fff;
    background-color: #0C0B38;
}

.login_dark_input_label_password {
    background-color: #0C0B38;
    position: absolute;
    left: 74vmin;
    top: 47.5vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.2vmin;
    letter-spacing: 0.05vmin;
    color: #fff;
}

.login_dark_email_input {
    background-color: #0C0B38;
    position: absolute;
    top: 32.2vmin;
    left: 68vmin;
    outline: 0;
    width: 67vmin;
    height: 9.7vmin;
    font-family: var(--font-family-Urbanist);

    border-radius: 2.9vmin;
    border: 0.55vmin solid transparent;

    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.8) 1.99%, rgba(36, 162, 233, 0.8) 85.34%, rgba(36, 162, 233, 0.8) 104.58%);
    -webkit-mask:
        linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
}

.login_dark_password_input {
    background-color: #0C0B38;
    position: absolute;
    left: 68vmin;
    top: 49.2vmin;
    outline: 0;
    width: 67vmin;
    height: 9.7vmin;
    border-radius: 2.9vmin;
    border: 0.55vmin solid transparent;
    background: linear-gradient(89.95deg, rgba(36, 162, 233, 0.8) 1.99%, rgba(50, 177, 38, 0.8) 104.58%);
    -webkit-mask:
        linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    font-family: var(--font-family-Urbanist)
}

.login_dark_forget_password {
    position: absolute;
    left: 109vmin;
    top: 62vmin;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.9vmin;
    letter-spacing: 0.05vmin;
    text-decoration-line: underline;
    text-transform: capitalize;
    color: #fff;
}

.login_dark_remember_text {
    position: absolute;
    left: 74vmin;
    top: 68vmin;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.9vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
    color: #fff;
}

.login_dark_remember_check {
    position: absolute;
    width: 3.4vmin;
    height: 3.4vmin;
    left: 69.2vmin;
    top: 68vmin;
    cursor: pointer;

}

.login_dark_btn_login {
    left: 94vmin;
    top: 74vmin;
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
    padding: 2.4vmin 4vmin;
    outline: 0;
    position: absolute;
    border-radius: 2.5vmin;
    border: none;
    font-family: var(--font-family-Comfortaa);
    font-size: 3.5vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: #FFFFFF;
}

.login_dark_btn_login:hover {
    background-color: #32B126;
}

.login_dark_dont_have_account {
    position: absolute;
    left: 74vmin;
    top: 85vmin;

    font-family: var(--font-family-Comfortaa);
    font-weight: 700;
    font-size: 2.9vmin;
    letter-spacing: 0.01vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: #5FD655;
}